/* styles.css */
@keyframes float-slow {
  0%, 100% { transform: translateY(0) translateX(0); }
  50% { transform: translateY(-50px) translateX(10px); }
}

@keyframes float-slow-delay {
  0%, 100% { transform: translateY(0) translateX(0); }
  50% { transform: translateY(-40px) translateX(-7px); }
}

@keyframes shine {
  0% { transform: translateX(-100%) skew(-15deg); }
  100% { transform: translateX(100%) skew(-15deg); }
}

.noise-bg::after {
  content: '';
  position: absolute;
  inset: 0;
  background: url('data:image/svg+xml;base64,');
  opacity: 0.15;
  pointer-events: none;
}

.animate-float-slow {
  animation: float-slow 15s ease-in-out infinite;
}

.animate-float-slow-delay {
  animation: float-slow-delay 18s ease-in-out infinite 3s;
}
